.center-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
}

.centered-input, .centered-text {
    font-size: 30px;
    text-align: center;
    padding: 5px;
    border-radius: 5px; /* 圓角 */
    border: 1px solid #e0e0e0; /* 細邊框，淡灰色 */
}

.error-message {
    color: red;
    text-align: center;
    margin-bottom: 10px;
}
